.ant-layout-content{
    width: 100%;
    height: 500px;
    border: 1px solid #dddddd  !important;
    
}

//division style
.ant-divider-horizontal{
    height: 8px;
    width: 510px;
    color: #DDDDDD  !important;
}

.ant-divider-inner-text{
    font-size: 18px;
    line-height: 18px;
    color:#40404C !important;

}



.ant-divider-horizontal.ant-divider-with-text::after {
    margin-right:  35px;
}

//搜索style
.app-search .ant-form{
    float: left;
    margin-bottom: 15px;
    margin-top: 15px;
}
.app-search{
    margin-left:350px ;
    width: 800px;
}

.ant-form-item-control:first-child:not([class^='ant-col-']):not([class*=' ant-col-']){
    width: 329px;
    height: 30px;
}

    
.ant-input-affix-wrapper{
    border: 1px solid #B4B4B4;
    border-radius: 5px;
}

.input-button{
    
    background: #40404C;
    border-radius: 5px;
    border: #40404C;
    height: 30px;
    width: 57px;
}

// .app-container .anticon svg{
//     color: #A1A1A1 ;
// }

.ant-input-affix-wrapper > input.ant-input{
    color: #A1A1A1 ;
    font-size: 14px;
}

//new-button
.new-button{
    margin-top: 15px;
    margin-right:100px ;
    background: #4367D8;
    border: 1px solid #979797;
    border-radius: 5px;
    float:right;
}

.handle-button{
    height: 24px;
    width: 42px;
    background: #FFFFFF;
    border: 1px solid #DEE0E8;
    border-radius: 12px;
    margin-left: 10px;
}

.handle-button span{
    font-size: 12px;
    color: #40404C ;
    position: relative;
    right: 9px;
    bottom:5px;
}

//改变气泡中附件按钮样式
.col-button .ant-upload{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #40404C;
    font-weight: 400;
}
.col-button .anticon{
    color: #000000 !important;
}

//表格文字样式
.ant-table-tbody .ant-table-cell{
    font-size: 12px;
    color: #40404C ;
    font-family: PingFangSC-Regular;
}

.ant-table-container table > thead > tr{
    font-family: PingFangSC-Medium;
    font-size: 14px;
    color: #40404C;
    text-align: center;
}


