//html编辑器
.html-BraftEditor{
    width: 463px;
    height: 300px;
    border: 1px solid #DDDDDD;
    border-radius: 5px;
    font-size: 14px;
}

//操作返回气泡
.ant-message-notice-content{
   
    margin-top:100px ;
    width: 382px;
    height: 54px;
    background: #F0F9EC;
