    border-radius: 5px;
}

.bf-content{
    height: 80% !important;
}


.ant-table{
    border-radius: 4px;
    margin: 21px 12px 0 12px;
}

.app-container .groupTitle{
    width: 72px;
    height: 25px;
    font-family: PingFangSC-Medium;
    font-size: 18px;
    color: #40404C;
    font-weight: 500;
}

.variableFirst .ant-col-4{
    flex: none;
    max-width: none;

